import Vue from 'vue'
import Router from 'vue-router'
import AllFiles from '../components/AllFiles/index'
import MyShares from '../components/MyShares/index'
import Recyclebin from '../components/Recyclebin/index'
import LoginForm from '../components/login/index'
import tokenUtil from '../utils/tokenUtil'
Vue.use(Router)


const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/files',
      name: 'AllFiles',
      component: AllFiles
    },
    {
      path: '/share',
      name: 'MyShares',
      component: MyShares
    },
    {
      path: '/recyclebin',
      name: 'RecycleBin',
      component: Recyclebin
    },
    {
      path: '/login',
      name: 'LoginForm',
      component: LoginForm
    }
  ]
})

/*router.beforeEach((to, from, next)=>{
  if(to.fullPath != '/login' && to.fullPath != '/register' && tokenUtil.getToken()==null){
    alert("你还没有登录")
    next("/login")
  }
  else{
    next()
  }
})*/

export default router



